import Animator from '../animator';
import FrameSet from '../animator/frameset';
import RepeatFrame from '../animator/RepeatFrame';
import AnimationFrame from '../animator/animationFrame';
import { loadImageByPromise } from '../util';
import { IMG } from './images'
import {
    BG_IMAGES,
    SPRITE_IMAGES,
    LOADING_PERFORMANCE_SPRITE_IMAGES,
    RENDERING_PERFORMANCE_PAINT_RECT_GPU,
    LOADING_PERFORMANCE_PRERENDER
} from './common';
import SPRITE from '../../spritesmith-generated/sprite.png';

const uniAnimator = new Animator();

function makeRepeatFrameByElements(frameset, framesetElement, containerWidth, step) {
    const img = framesetElement.getElementsByClassName('obj-1')[0];
    const imgWidth = img.offsetWidth;
    const wideWidth = containerWidth;
    const necessaryCount = parseInt(wideWidth / imgWidth + (containerWidth % imgWidth > 0 ? 1 : 0), 10);
    const fitWidth = containerWidth + (containerWidth % imgWidth);

    if (necessaryCount > 10) {
        throw 'error';
    }

    // one more object than necessary FitWidth
    for (let i = 0; i < necessaryCount; i += 1) {
        const element = img.cloneNode();
        framesetElement.appendChild(element);
    }

    frameset.addFrame(new RepeatFrame(fitWidth, framesetElement, step));
}

function makeAnimationFrame(frameset, framesetElement, containerWidth, x, movingHeight, step) {
    frameset.addFrame(new AnimationFrame(containerWidth, framesetElement, x, movingHeight, step));
}

function makeFrameParent(container, classList) {
    const frameParent = document.createElement('div');

    classList.forEach(className => {
        frameParent.classList.add(className);
    });

    container.appendChild(frameParent);
    
    return frameParent;
}

function generateScrollingFrame(container, {classList, selector, src, step}) {
    const containerWidth = container.offsetWidth;
    const frameset = new FrameSet();
    const frameParent = makeFrameParent(container, classList);
    const imgClassList = ['obj-1'];

    if (LOADING_PERFORMANCE_SPRITE_IMAGES) {
        imgClassList.push(selector);
    }

    uniAnimator.addFrameSet(frameset);

    return loadImageByPromise(frameParent, imgClassList, src, frameset)
        .then(() => {
            if (step) {
                makeRepeatFrameByElements(frameset, frameParent, containerWidth, step);
            }

            return Promise.resolve(frameset);
        });
}

function generateMovingSprite(container, {classList, selector, src}) {
    const frameset = new FrameSet();
    const frameParent = makeFrameParent(container, classList);
    const animationCount = selector.length;
    const promises = [];

    uniAnimator.addFrameSet(frameset);

    for (let i = 0; i < animationCount; i++) {
        const classList = ['obj-1'];
        if (LOADING_PERFORMANCE_SPRITE_IMAGES) {
            classList.push(selector[i]);
        }
        promises.push(loadImageByPromise(frameParent, classList, src[i], frameset));
    }

    return Promise.all(promises);
}

function loadImage(imgSrc) {
    return new Promise(resolve => {
        const img = document.createElement('img');
        img.src = imgSrc;

        img.onload = () => {
            resolve();
        };
    });
}

function loadImages() {
    let promise;

    if (LOADING_PERFORMANCE_SPRITE_IMAGES) {
        promise = loadImage(SPRITE);
    } else {
        let images = [IMG.BACK_SKY, IMG.BACKGROUND, IMG.FOREST, IMG.GROUND].concat(IMG.CAT).concat(IMG.DINO).concat(IMG.DOG).concat(IMG.RED_BEE).concat(IMG.YELLOW_BEE)
        promise = Promise.all(images.map(image => loadImage(image)))
    }

    return promise;
}

function generateMovingSprites(container) {
    let animationContainer;

    if (RENDERING_PERFORMANCE_PAINT_RECT_GPU) {
        animationContainer = document.createElement('div');
        animationContainer.className = 'animation-container';
        container.appendChild(animationContainer);
    } else {
        animationContainer = container;
    }

    return Promise.all(SPRITE_IMAGES.map(sprite => {
        return generateMovingSprite(animationContainer, sprite).then(([imageInfo]) => {
            const containerWidth = animationContainer.offsetWidth;
            const { frameParent, frameset } = imageInfo;
            const { movingHeight, spriteCount, step } = sprite;

            makeAnimationFrame(frameset, frameParent, containerWidth, parseInt(Math.random() * 400, 10), movingHeight, step);

            if (spriteCount) {
                for (let i = 0; i < spriteCount; i += 1) {
                    frameset.addFrame(frameset.frames[0].clone(parseInt(Math.random() * 400, 10), parseInt(Math.random() * 5, 10)));
                }
            }
        });
    }));
}

function createTitle(container) {
    return new Promise(resolve => {
        const titleEl = document.createElement('div');
        titleEl.classList.add('title');
        titleEl.innerHTML = `<div>NHN Forward 2018</div><div>FE Performance Optimization</div>`
        container.appendChild(titleEl);

        resolve();
    });
}

function generate(container) {
    return createTitle(container).then(() => {
        return Promise.all(BG_IMAGES.map(bgImage => {
            return generateScrollingFrame(container, bgImage);
        }));
    }).then(() => {
        return generateMovingSprites(container);
    }).then(() => {
        if (LOADING_PERFORMANCE_PRERENDER) {
            return Promise.resolve();
        }

        return loadImages();
    }).then(() => {
        if (!LOADING_PERFORMANCE_PRERENDER) {
            document.querySelectorAll('.loading').forEach(img => {
                img.src = img.dataset.src;
                img.classList.remove('loading')
            })
        }

        return Promise.resolve(uniAnimator);
    });
}

export default {
    generate
};
