import jQuery from 'jquery';
import 'tui-code-snippet';
import 'lodash';
import 'moment';

import 'css/main.css';
import basic from 'js/basic';
import {
    LOADING_PERFORMANCE_PREVIEW_IMAGE
} from 'js/basic/common';

function loadMainComponentByPreRender() {
    const container = document.getElementById('container');

    return new Promise(resolve => {
        basic.generate(container).then(() => {
            resolve(container.innerHTML);
        });
    })
}

jQuery(() => {
    const container = document.getElementById('container');

    if (LOADING_PERFORMANCE_PREVIEW_IMAGE) {
        container.innerHTML = '';
    }

    basic.generate(container).then(animator => {
        const progress = document.getElementById('progress');
        if (progress) {
            progress.remove();
        }

        animator.run();
    });
});

export default loadMainComponentByPreRender;
