import {doHeavyJob} from './longTimeJob';
import MyWorker from 'worker-loader!../worker/job.worker';
import { RENDERING_PERFORMANCE_WEB_WORKER } from '../basic/common';

const DEFAULT_INTERVAL = 16;

export default class Animator {
    constructor(container) {
        this.requestId = null;

        this.frameSetList = [];

        this.updateInterval = DEFAULT_INTERVAL;

        this.interval = null;

        this.drawCallback = this.onDraw.bind(this);

        this.worker = null;
    }

    addFrameSet(frameset) {
        this.frameSetList.push(frameset);
    }

    init() {
        this.frameSetList.forEach(frameset => {
            frameset.init();
        });
    }

    run() {
        if (this.interval) {
            this.stop();
        }

        this.init();

        window.addEventListener('beforeunload', () => this.onUnload())
        
        this.requestId = window.requestAnimationFrame(this.drawCallback);
        // this.interval = setInterval(this.drawCallback, this.updateInterval);
    }

    runWorker() {
        if (!this.worker) {
            this.worker = new MyWorker();

            this.worker.onmessage = event => {};
            this.worker.addEventListener('message', this.onMessage.bind(this));
        }

        this.worker.postMessage({});
    }

    onMessage(event) {
        console.log(event);
    }

    calculatePositions() {
        if (RENDERING_PERFORMANCE_WEB_WORKER) {
            this.runWorker();
        } else {
            // console.time('doHeavyJob');
            doHeavyJob();
            // console.timeEnd('doHeavyJob');
        }
    }

    onDraw() {
        // RENDERING_PERFORMANCE_WEB_WORKER를 테스트할 때 경우 켠다.
        this.calculatePositions();

        this.frameSetList.forEach(frameset => {
            frameset.onDraw();
        });

        if (!this.interval) {
            this.requestId = window.requestAnimationFrame(this.drawCallback);
        }
    }

    onUnload() {
        this.interval = null;
        window.cancelAnimationFrame(this.requestId);
    }

    stop() {
        this.frameSetList.forEach(frameset => {
            frameset.stop();
        });
    }
}
